:root {
  --primary: #E2B857;
  --primary-light: #F5D77E;
  --primary-dark: #C9A227;
  --bg-primary: #0B1121;
  --bg-secondary: #0F172A;
  --bg-tertiary: #1E293B;
  --text-primary: #F8FAFC;
  --text-secondary: #94A3B8;
  --text-muted: #64748B;
  --border: rgba(255, 255, 255, 0.06);
  --border-hover: rgba(255, 255, 255, 0.12);
  --accent-green: #22C55E;
  --accent-blue: #3B82F6;
  --accent-purple: #8B5CF6;
}
html {
  scroll-behavior: smooth;
}
body {
  background: var(--bg-primary);
  color: var(--text-primary);
  margin: 0;
  padding: 0;
}
.text-gold-gradient {
  background: linear-gradient(135deg, #F5D77E 0%, #E2B857 40%, #C9A227 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}
.border-gold-animated {
  position: relative;
  background: var(--bg-secondary);
  border-radius: 16px;
}
.border-gold-animated::before {
  content: '';
  position: absolute;
  inset: -1px;
  background: linear-gradient(135deg, #E2B857, #F5D77E, #C9A227);
  border-radius: 17px;
  z-index: -1;
  opacity: 0.8;
}
@keyframes shimmer {
  0% {
    background-position: 0% 50%;
  }
  100% {
    background-position: 200% 50%;
  }
}
@keyframes float {
  0%, 100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-12px);
  }
}
.animate-float {
  animation: float 5s ease-in-out infinite;
}
.glass {
  background: rgba(255, 255, 255, 0.03);
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
  border: 1px solid rgba(255, 255, 255, 0.06);
  transition: all 0.2s ease;
}
.glass:hover {
  background: rgba(255, 255, 255, 0.05);
  border-color: rgba(255, 255, 255, 0.1);
}
.metal-card {
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  border: 1px solid transparent;
}
.metal-card:hover {
  transform: translateY(-4px);
  border-color: rgba(226, 184, 87, 0.2);
  box-shadow: 0 20px 40px -12px rgba(0, 0, 0, 0.4),
    0 0 0 1px rgba(226, 184, 87, 0.1);
}
.hero-gradient {
  background: radial-gradient(ellipse 100% 80% at 50% -30%, rgba(226, 184, 87, 0.12), transparent 70%),
    radial-gradient(ellipse 60% 50% at 100% 50%, rgba(139, 92, 246, 0.05), transparent 60%),
    radial-gradient(ellipse 60% 50% at 0% 80%, rgba(59, 130, 246, 0.04), transparent 60%);
}
.trust-badge {
  background: rgba(226, 184, 87, 0.08);
  border: 1px solid rgba(226, 184, 87, 0.15);
  transition: all 0.2s ease;
}
.trust-badge:hover {
  background: rgba(226, 184, 87, 0.12);
  border-color: rgba(226, 184, 87, 0.25);
}
.nav-link {
  position: relative;
}
.nav-link::after {
  content: '';
  position: absolute;
  bottom: -4px;
  left: 0;
  width: 0;
  height: 2px;
  background: linear-gradient(90deg, #E2B857, #F5D77E);
  border-radius: 1px;
  transition: width 0.3s ease;
}
.nav-link:hover::after {
  width: 100%;
}
.section-divider {
  height: 1px;
  background: linear-gradient(90deg, transparent, rgba(226, 184, 87, 0.2), transparent);
}
.btn-primary {
  background: linear-gradient(135deg, #E2B857 0%, #C9A227 100%);
  color: #09090B;
  border: none;
  transition: all 0.2s ease;
  box-shadow: 0 4px 20px rgba(226, 184, 87, 0.25);
}
.btn-primary:hover {
  box-shadow: 0 8px 30px rgba(226, 184, 87, 0.35);
  transform: translateY(-1px);
}
.btn-secondary {
  background: rgba(255, 255, 255, 0.04);
  border: 1px solid rgba(255, 255, 255, 0.08);
  color: var(--text-primary);
  transition: all 0.2s ease;
}
.btn-secondary:hover {
  background: rgba(255, 255, 255, 0.08);
  border-color: rgba(255, 255, 255, 0.15);
}
.card {
  background: rgba(255, 255, 255, 0.02);
  border: 1px solid rgba(255, 255, 255, 0.05);
  border-radius: 16px;
  transition: all 0.3s ease;
}
.card:hover {
  background: rgba(255, 255, 255, 0.03);
  border-color: rgba(255, 255, 255, 0.08);
}
@media (max-width: 1024px) {
  .grid-4 {
    grid-template-columns: repeat(2, 1fr) !important;
  }
  .grid-3 {
    grid-template-columns: repeat(2, 1fr) !important;
  }
  .hero-grid {
    grid-template-columns: 1fr !important;
    gap: 40px !important;
  }
  .hero-title {
    font-size: 42px !important;
  }
  .section-title {
    font-size: 32px !important;
  }
  .stat-value {
    font-size: 32px !important;
  }
}
@media (max-width: 768px) {
  .grid-4 {
    grid-template-columns: repeat(2, 1fr) !important;
  }
  .grid-3 {
    grid-template-columns: 1fr !important;
  }
  .grid-2 {
    grid-template-columns: 1fr !important;
  }
  .hero-title {
    font-size: 32px !important;
    line-height: 1.1 !important;
  }
  .section-title {
    font-size: 24px !important;
  }
  .stat-value {
    font-size: 24px !important;
  }
  .stats-grid {
    grid-template-columns: repeat(2, 1fr) !important;
    gap: 20px !important;
  }
  .hide-mobile {
    display: none !important;
  }
  .show-mobile {
    display: flex !important;
  }
  .mobile-padding {
    padding-left: 16px !important;
    padding-right: 16px !important;
  }
  .mobile-pt {
    padding-top: 130px !important;
  }
  .cta-buttons {
    flex-direction: column !important;
  }
  .cta-buttons a {
    width: 100% !important;
    justify-content: center !important;
  }
  .hero-badge {
    font-size: 11px !important;
    padding: 8px 12px !important;
  }
  .hero-subtitle {
    font-size: 15px !important;
  }
  section {
    padding: 60px 0 !important;
  }
}
@media (max-width: 480px) {
  .hero-title {
    font-size: 28px !important;
  }
  .section-title {
    font-size: 22px !important;
  }
  .stats-grid {
    grid-template-columns: repeat(2, 1fr) !important;
  }
  .grid-4 {
    grid-template-columns: 1fr !important;
  }
}
.mobile-menu {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(11, 17, 33, 0.98);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  z-index: 100;
  display: flex;
  flex-direction: column;
  padding: 100px 24px 40px;
  transform: translateX(100%);
  transition: transform 0.3s ease;
}
.mobile-menu.open {
  transform: translateX(0);
}
.mobile-menu a {
  color: var(--text-primary);
  font-size: 18px;
  padding: 14px 0;
  border-bottom: 1px solid rgba(255,255,255,0.06);
  text-decoration: none;
  font-family: 'Space Grotesk', sans-serif;
  font-weight: 500;
  transition: color 0.2s ease;
}
.mobile-menu a:hover {
  color: #E2B857;
}
.hamburger {
  display: none;
  flex-direction: column;
  gap: 5px;
  cursor: pointer;
  padding: 8px;
  z-index: 101;
}
.hamburger span {
  width: 22px;
  height: 2px;
  background: var(--text-primary);
  border-radius: 1px;
  transition: all 0.3s ease;
}
.hamburger.open span:nth-child(1) {
  transform: rotate(45deg) translate(5px, 5px);
}
.hamburger.open span:nth-child(2) {
  opacity: 0;
}
.hamburger.open span:nth-child(3) {
  transform: rotate(-45deg) translate(5px, -5px);
}
@media (max-width: 768px) {
  .hamburger {
    display: flex;
  }
  .desktop-nav {
    display: none !important;
  }
  .footer-grid {
    grid-template-columns: 1fr 1fr !important;
    gap: 24px !important;
  }
  .footer-brand {
    grid-column: 1 / -1;
    margin-bottom: 16px;
  }
}
[dir="rtl"] {
  direction: rtl;
  text-align: right;
}
[dir="rtl"] .hero-grid {
  direction: rtl;
}
[dir="rtl"] .cta-buttons {
  flex-direction: row-reverse;
}
[dir="rtl"] .nav-link::after {
  left: auto;
  right: 0;
}
[dir="rtl"] svg {
  transform: scaleX(-1);
}
[dir="rtl"] .metal-card,
[dir="rtl"] .card {
  text-align: right;
}
[dir="rtl"] .footer-grid {
  direction: rtl;
}
[dir="rtl"] .mobile-menu {
  transform: translateX(-100%);
}
[dir="rtl"] .mobile-menu.open {
  transform: translateX(0);
}
[dir="rtl"] .hamburger.open span:nth-child(1) {
  transform: rotate(-45deg) translate(-5px, 5px);
}
[dir="rtl"] .hamburger.open span:nth-child(3) {
  transform: rotate(45deg) translate(-5px, -5px);
}
[dir="rtl"] [style*="flex"] {
  flex-direction: row-reverse;
}
[dir="rtl"] [style*="gap"] {
  gap: inherit;
}
[dir="rtl"] .stats-grid,
[dir="rtl"] .grid-4,
[dir="rtl"] .grid-3,
[dir="rtl"] .grid-2 {
  direction: ltr;
}
[dir="rtl"] .stats-grid > *,
[dir="rtl"] .grid-4 > *,
[dir="rtl"] .grid-3 > *,
[dir="rtl"] .grid-2 > * {
  direction: rtl;
  text-align: right;
}
